.image-abs-center{
	position: relative;
	overflow: hidden;
	& > a {
		display: block;
	}
	& > a > img,
	& > img {
		position: absolute;
		left: 50%;
		top: 50%;
		height: 110%;
		max-height: 110%;
		width: auto;
		max-width: initial;
		-webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

	}
	&.by-width{
		& > img {
			width: 110%;
			max-width: 110%;
			height: auto;
			max-height: initial;
		}
	}
}

