.backurl{
	a{
		border: 0.4px solid #acacac;
		padding: 0 5px;
		border-radius: 3px;
		line-height: 20px;
		height: 20px;
		font-size: 0.6em;
		text-transform: uppercase;
		display: block;
		color: black;
		font-weight: bold;
		text-decoration: none;
		float: left;
		margin: 0 5px 0 0;

		&:hover{
			background: $color-blue;
			border: 0.4px solid $color-blue;
			color: white;
		}
	}
}

.slideShow {
	position: relative;
	background: #FFFFFF;
	width: 870px;

	.info-box {
		position: absolute;
		color: white;
		top: 20%;
		left: 50%;
		width: $page-width;
		z-index: 1000;
		pointer-events:none;
		-webkit-transform: translateX(-50%);
        transform: translateX(-50%);

		.strip {
			position: relative;
			float: left;

			& > div:not(.more-link) {
				float: left;
			}
			.main {
				background: $color-red;
				text-transform: uppercase;
				font-weight: bold;
		    padding: 15px;
		    font-size: 3em;
		    line-height: 1.2;
			}
			.second{
				background: $color-grey;
				padding: 8px;
				font-size: 1.6em;
				line-height: 1.2;
				margin-top: 10px;
			}
		}
	}

	.more-link a {
		background: $color-pr;
		padding: 5px 10px;
		color: white;
		font-size: 1em;
		font-family: $font-cond;
		text-transform: uppercase;
		text-decoration: none;
		pointer-events:auto;

		@include border-radius(5px);
	}
	.more-link {
		position: absolute;
		right: 0;
		bottom: -50px;
	}

	.slick-dots {
		position: absolute;
		bottom: 15px;
		left: 50%;
		-webkit-transform: translateX(-50%);
        transform: translateX(-50%);
		z-index: 10;
	}
	.slick-slide {
		position: relative;
	}
	.slick-dots li {
		float: left;
		margin: 0;
		display: block;
	}
	.slick-dots li button {
		display: block;
    width: 12px;
    height: 0;
    padding-top: 12px;
    background-position: 0 0;
    float: left;
    overflow: hidden;
    position: relative;
    border:none;
    background: transparent;
	}

	.slick-dots li button:before {
		color: #9e9e9e;
		font-size:10px;
		line-height: 12px;
		width: 12px;
		content: "\f111";
		font-family: FontAwesome;
		position: absolute;
		left: 0;
		text-align: center;
		top: 0;
		text-shadow: none;
	}
	.slick-dots li.slick-active button:before {
		color: $color-blue;
		font-size:12px;
		content: "\f111";
		font-family: FontAwesome;
	}
	.slidesjs-navigation {
		position: absolute;
		top: 0;
		z-index: 1000;
	}
	.slick-prev,
	.slick-next
	{
		background: $color-blue;
		z-index: 1;
		width: 48px;
		height: 48px;
		position: absolute;
		font-size: 28px;
		color: transparent !important;
		top: 50%;
		-webkit-transform: translateY(-50%);
          transform: translateY(-50%);
		color: white;
		opacity:1;
	  filter:alpha(opacity=100);
	  border: none;
	}
	.slick-prev {
		left: 10px;
	}
	.slick-next {
		right: 10px;
	}
	.slick-prev:before {
		content: "\f104";
		font-size: 40px;
    width: 48px;
    text-align: center;
    line-height: 26px;
		position: absolute;
		font-family: FontAwesome;
		color: white;
		top: 10px;
		left: 0;
	}
	.slick-next:before {
		content: "\f105";
		font-size: 40px;
    width: 48px;
    text-align: center;
    line-height: 26px;
		position: absolute;
		font-family: FontAwesome;
		color: white;
		top: 10px;
		right: 0;
	}
	.slick-prev:hover,
	.slick-next:hover{
		 opacity:1;
	  	filter:alpha(opacity=100);
	}

	.slidesjs-stop,
	.slidesjs-play {
		display: block;
    width: 18px;
    height: 0;
    padding-top: 18px;
    background-position: 0 0;
    overflow: hidden;
    position: absolute;
    bottom: 10px;
    top: auto;
    left: 10px;
    z-index: 1000;
	}
	.slidesjs-stop:before,
	.slidesjs-play:before {
		color: #fff;
		font-size:15px;
		line-height: 18px;
		width: 18px;
		content: "\f04c";
		font-family: FontAwesome;
		position: absolute;
		left: 0;
		text-align: center;
		top: 0;
		text-shadow: 0 0 5px $color-pr;
	}
	.slidesjs-play:before {
		content: "\f04b";
	}
}
