.news,
.news-list {
	background: #f1f1f1;
	border-top: 3px solid #dbdbdb;
	padding: 20px 0 50px 0;
	position: relative;

	.slick-dots{
		position: absolute;
		bottom: -33px;
		left: 50%;
		z-index: 1;
		-webkit-transform: translateX(-50%);
          transform: translateX(-50%);

		& > li {
			display: inline-block;
			*display: inline;
			zoom: 1;
			vertical-align: middle;
	 		padding: 0 4px;

			& > button{
				position: relative;
				background: transparent;
				border: none;
				text-indent: -9999px;
				&:after{
					font-family: 'FontAwesome';
					content:"\f111";
					font-size: 14px;
					width: 16px;
					height: 16px;
					line-height: 16px;
					top: 50%;
					color:#bfbfbf;
					left: 50%;
					text-indent: initial;
					position: absolute;
					-webkit-transform: translate(-50%,-50%);
					transform: translate(-50%,-50%);
				}
			}

			&.slick-active{
				& > button{
					&:after{
						color: $color-orange;
					}
				}
			}
		}
	}

	.slick-arrow{
		position: absolute;
		bottom: -35px;
		width: 25px;
		height: 25px;
		border: none;
		background: white;
		text-indent: -9999px;
		color: #4c4c4c;
		z-index: 2;

		&:after{
			font-family: 'FontAwesome';
			font-size: 20px;
			width: 25px;
			height: 25px;
			line-height: 25px;
			top: 50%;
			color: $color-orange;
			left: 50%;
    	text-indent: initial;
			position: absolute;
			-webkit-transform: translate(-50%,-50%);
			transform: translate(-50%,-50%);
		}

		&.slick-prev{
			right: 43px;
			&:after{
				content: "\f104";
			}
		}
		&.slick-next{
			right: 15px;
			&:after{
				content: "\f105";
			}
		}

		&:hover{
			background: $color-orange;
			color: white;

			&:after{
				color: white;
			}
		}


	}

	.articles {
		margin: 0 -5px;
		article{
			padding: 0 15px;

			& > .wrapper{
