					h2 {
						font-size: 1.2em;
						position: absolute;
						top: 50%;
						width: 100%;
						-webkit-transform: translateY(-50%);
          	transform: translateY(-50%);
					}
				}

				h2 {
					text-align: center;
					text-transform: uppercase;
					margin: 0;
					padding: 0 10px;

					a{
						color: #6e6e6e;
						text-decoration: none;
					}
				}

				.content{
					padding: 0 10px;
					font-size: 0.8em;
					line-height: 1.2;
					text-align: justify;
					height: 70px;
					overflow: hidden;
					position: relative;

					&:after{
						content:"";
						height: 20px;
						bottom: 0;
						left: 1px;
						right: 1px;
						position: absolute;
						/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&0+0,1+70,1+70,1+100 */
						background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 70%, rgba(255,255,255,1) 100%); /* FF3.6-15 */
						background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 70%,rgba(255,255,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
						background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 70%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
						filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
					}
				}

				.more{
					text-align: center;
					padding: 15px 10px 25px 10px;

					a{
						padding: 10px 15px;
						color: white;
						text-transform: uppercase;
						text-decoration: none;
						font-weight: bold;
						font-size: 0.9em;
						border-radius: 5px;
						background: #6e6e6e;
					}
				}
			}
			&.no-img{
				& > .wrapper{
					.img{
						img {
							width: auto;
							height: 100%;
						}
					}
				}
			}

			&:hover{
				& > .wrapper{
					background: #c8cbcc;
					border: 0.5px solid $color-orange;

					.content{
						&:after{
							/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#c8cbcc+0,c8cbcc+100&0+0,1+70,1+70,1+100 */
							background: -moz-linear-gradient(top, rgba(200,203,204,0) 0%, rgba(200,203,204,1) 70%, rgba(200,203,204,1) 100%); /* FF3.6-15 */
