							filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00c8cbcc', endColorstr='#c8cbcc',GradientType=0 ); /* IE6-9 */
						}
					}
					.more{
						a{
							background: $color-orange;
						}
					}
				}
			}
		}
	}
}

.galleries{
	padding: 20px 0 50px 0;

	.gallery-folders{
		& > .holder{
			h2{
				margin: 10px 0 25px 0;
				color: black;
				text-align: center;
			}

			.folders{
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				flex-wrap: wrap;
				align-items: flex-start;
				margin: -10px;

				& > .folder{
					padding: 10px;
					flex-basis: calc(100% / 4);

					& > .wrapper{
						background: white;
						border: 0.5px solid #d3d3d3;
						overflow: hidden;
						border-radius: 2px;
						box-shadow: 0 4px 20px -5px #c1c1c1;

						& > a {
							display: block;
							text-decoration: none;

							.image{
								overflow: hidden;
								position: relative;

								.imagenum{
									z-index: 10;
									position: absolute;
									right: 5px;
									bottom: 5px;
									background: rgba(0, 0, 0, 0.56);
									padding: 4px;
									border-radius: 3px;
									color: white;
								}

								img {
									position: absolute;
									max-height: 100%;
									height: 100%;
									width: auto;
									max-width: initial;
									left: 50%;
									top: 50%;
									-webkit-transform: translate(-50%,-50%);
          				transform: translate(-50%,-50%);
								}
							}

							.title{
								height: 55px;
								overflow: hidden;
								max-height: 55px;
								padding: 15px;
								text-transform: uppercase;
								font-weight: bold;
								color: black;
								text-align: center;
								background: #f8f8f8;
							}

							&:hover{
								.title{
									color: $color-orange;
								}
							}
						}
					}
				}
			}
		}
	}
	.gallery-list{
		& > .pw {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			flex-wrap: wrap;

			.sidebar{
				flex-basis: 20%;
			}

			.gallery-items{
				flex: 1;
				padding: 0 0 0 15px;

				.images{
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					flex-wrap: wrap;
					margin: 10px -5px;
					& > .image{
						flex-basis: calc(100% / 3);
						padding: 5px;
						& > .wrapper{
							a {
								display: block;
								position: relative;
								height: 100%;
								width: 100%;
								overflow: hidden;
							}

							img{
								position: absolute;
								max-height: 100%;
								height: 100%;
								width: auto;
								max-width: initial;
								left: 50%;
								top: 50%;
								-webkit-transform: translate(-50%,-50%);
								transform: translate(-50%,-50%);
							}
						}
					}
				}
			}
		}
	}
}

