.webshop-product-top.slide-style{
	position: relative;
	.items {
		margin: 0;
		.item {
			flex-basis: 100%;
		}
	}
	.slick-dots{
		position: absolute;
		right: 70px;
		top: -42px;

		li {
			width: 25px;
			height: 25px;
			background: white;
			display: inline-block;
			*display: inline;
			zoom: 1;
			margin-left: 5px;

			button {
				width: 25px;
				height: 25px;
				border:none;
				text-align: center;
				color: #555555;
				background: none;
				font-weight: bold;
				font-size: 0.9em;
			}
			&.slick-active{
				background: #333333;
				button {
					color: white;
				}
			}
			&:hover{
				background: #666666;
				button {
					color: white;
				}
			}
		}
	}

	.slick-arrow{
		position: absolute;
		top: -42px;
		width: 25px;
		height: 25px;
		border: none;
		background: white;
		text-indent: -9999px;
		color: #555555;
		z-index: 2;

		&:after{
			font-family: 'FontAwesome';
			font-size: 16px;
			width: 25px;
			height: 25px;
			line-height: 25px;
			top: 50%;
			color: #555555;
			left: 50%;
    	text-indent: initial;
			position: absolute;
			-webkit-transform: translate(-50%,-50%);
			transform: translate(-50%,-50%);
		}

		&.slick-prev{
			right: 40px;
			&:after{
				content: "\f104";
			}
		}
		&.slick-next{
			right: 10px;
			&:after{
				content: "\f105";
			}
		}

		&:hover{
			background: #333333;
			color: white;

			&:after{
				color: white;
			}
		}
	}
}
