		position: absolute;
		top: 50%;
		bottom: 0;
		height: 30px;
		text-indent: -1000px;
		border: none;
		background: $color-blue;
		width: 30px;
		z-index: 10;
		overflow: hidden;
		-webkit-transform: translateY(-50%);
					transform: translateY(-50%);
		&:before{
			color: #ffffff;
			font-family: 'FontAwesome';
			font-size: 30px;
     	margin-top: -1px;
			position: absolute;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%,-50%);
	          transform: translate(-50%,-50%);
			text-indent: 0;
		}

		&:hover{
			&:before{
				color: black;
			}
		}
	}
	.slick-prev{
		left: -45px;
		&:before{
			content: "\f104";
		}
	}
	.slick-next{
