		overflow: auto;
		height: 100%;
		width: 100%;
		& > ul > li > a {
			display: block;
			color: white;
			text-transform: uppercase;
			font-weight: 300;
			padding: 15px;
			font-size: 4.5vw;
			border-bottom: 0.5px solid #272727;

			&:hover,&:focus{
				 background: $color-orange;
				 text-decoration: none;
			}
		}
		.nav-sub-view{
			display: none;
			padding: 0 0 0 15px;
			& > .inside{
				border-left: 2px solid #868686;
			}
			ul > li > a {
				display: block;
				color: #959595;
				font-weight: 300;
				padding: 10px;
				font-size: 3.5vw;
				border-bottom: 0.5px solid #272727;

				&:hover,&:focus{
					 background: $color-orange;
					 text-decoration: none;
				}
			}
		}

		& > ul > li:hover .nav-sub-view{
			display: block;
			a{
				color: white;
			}
		}
	}
	&.opened{
		opacity: 1;
		filter:alpha(opacity=100);
		pointer-events: auto;
	}
}

.mobil-nav{
	position: absolute;
	left: 10px;
	top: 10px;
	font-size: 28px;
	color: $color-orange;
}
